define('pomodoro-electron/routes/data', ['exports'], function (exports) {
  function createTasks() {
    var obj = { tasks: [] };
    for (var i = 1; i < 6; i++) {
      obj.tasks.push({ id: i + '', name: 'Task ' + i,
        pomodoros: [] });
      for (var j = 1; j < 6; j++) {
        obj.tasks[i - 1].pomodoros.push({ date: new Date() });
      }
    }
    return obj;
  }

  exports['default'] = createTasks();
});